<template>
    <div>
        
        <div class="header"></div>

        <div class="content-box">

            <div class="header-content">
                <div class="register"  @click="click">
                    <!-- <router-link to="/register"><span class="register-title" >{{text}}</span></router-link> -->
                    <span class="register-title" >{{text}}</span>
                </div>

                <div class="set-up" v-show="isshow" @click="SignOut">
                    <span>退出</span>
                </div>
            </div>

            <div class="frequently-used-list">
                <div class="list-header">
                    <span class="fl">常用</span>
                    <span class="fr">查看全部></span>
                </div>

                <div class="list-content">
                    <van-grid :column-num="4">
                        <van-grid-item
                            v-for="(item,index) in userListData"
                            :key="index"
                            :icon="item.icon"
                            :text="item.title"
                            @click="list({name:item.type})"
                        />
                    </van-grid>
                </div>

            </div>

            <div class="list-find">
                <span class="fl">发现</span>
                <span class="fr">查看全部></span>
            </div>

            <div>
                <router-view />
            </div>           
        </div>
        
    </div>  
</template>

<script>

    import { userListData } from '../../data/usedListData.js'

export default {
    name:'mine',
    data(){
        return {
            userListData:[],
            isLogin:'',
            text:'去登录',
            isshow:false
            
        }
    },
    created(){
        this.userListData = userListData   
        
        let isLogin = localStorage.getItem('isLogin')
        this.isLogin = JSON.parse(isLogin)
        if(this.isLogin){
            this.isshow = true
            this.text = '您已登录'
        }
    },

    methods:{

        list(o){
            this.$router.push(o)
        },

        // 登录事件
        click(){
            if(this.isLogin === false || this.isLogin == null){
                this.$router.push({name:'register'})
            }
        },
        // 退出事件
        SignOut(){
                this.text = '去登录',
                this.isLogin = false
                this.isshow = false
                // 保存在本地
                localStorage.setItem('isLogin',JSON.stringify(this.isLogin))
        }
    }
};
</script>

<style lang="less" scoped>
.header {
  height: 24px;
}
.content-box{
    background: #fafafa;
    overflow: hidden;
    position: relative;
}
.header-content {
  height: 5rem;
  width: 100%;
  background: #fff;
  position: relative;

  .register {
    width: 3rem;
    height: 3rem;
    background: #ff3539;
    border-radius: 50%;
    margin: 0 auto;
    position: absolute;
    top: 1rem;
    left: 0;
    right: 0;
    text-align: center;
    line-height: 3rem;
    font-size: 0.6rem;
    
    .register-title{
      color: #fff;  
    }
        
  }
  .set-up {
    position: absolute;
    right: 0.3rem;
    // top: 0.1rem;
    font-size: .5rem;
  }
}

.frequently-used-list{
    margin-top: .3rem;
    background: #fff;
    .list-header{
        height: 1.5rem;
        width:calc(100% - 1rem);
        border-bottom:1px solid#f5f5f5;
        font-size: .5rem;
        line-height: 1.5rem;
        padding:0 .5rem;

        >span:nth-child(2){
            color: #A6A6A6;
        }      
    }
    .list-content{
        color: #000
    }
}

.list-find{
    height: 1.5rem;
    width:calc(100% - 1rem);
    background: #FFF;
    // border-bottom:1px solid#f5f5f5;
    font-size: .5rem;
    line-height: 1.5rem;
    padding:0 .5rem;
    margin-top: .3rem;

    >span:nth-child(2){
        color: #A6A6A6;
    }       
}


</style>